<template>
  <div class="dashboard">
    <div class="user-brief-wrapper clearfix">
      <div class="user-brief-pic">
        <img src="data:image/jpeg;base64," alt="" width="160" height="160">
      </div>
      <div class="user-brief">
        <h2>欢迎您，{{ user.username }}</h2>
        <div class="info clearfix">
          <!--<h4>当前文章数 <router-link to="/article"><el-tag type="primary">{{ user.artcleCount }}</el-tag></router-link></h4>-->
          <!--<h4>状态更新最后日期 <router-link to="/status-panel"><el-tag>{{ user.statusDate }}</el-tag></router-link></h4>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
      user: {
        username: Store.state.user.username,
        artcleCount: 11,
        statusDate: '2017-5-21'
      }
    };
  }
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .dashboard {
    .user-brief-pic {
      float: left;
    }
    .user-brief {
      float: left;
      margin-left: 90px;
      h2 {
        font-size: 46px;
      }
      .info {
        margin-top: 50px;
        font-size: 18px;
        h4 {
          float: left;
          margin-right: 40px;
          .el-tag {
            font-size: 18px;
          }
        }
      }
    }
  }
</style>



